import React, { useState } from 'react';
import '../components/Register.css';
import { Input } from "antd";
import { useNavigate } from 'react-router-dom';
import { firebase } from '.././firebase';
import axios from 'axios';
import { useAuth } from '../middleware/Auth';
import apiAddress from './IP';

function RegisterScreen() {
    const navigate = useNavigate();
    const { login } = useAuth();

    const [firstname, setFirstname] = useState("");
    const [lastname, setLastname] = useState("");
    const [license, setLicense] = useState("");
    const [tel, setTel] = useState("");
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");
    const [confirm, setConfirm] = useState("");
    const [image, setImage] = useState(null);
   
    const defaultImage = "https://cdn-icons-png.flaticon.com/512/147/147131.png";
    const defaultBackground = "https://jamie-wong.com/images/color/Purple.png";

    const handleSignup = async () => {
        // 
        if (!firstname || !lastname || !license || !tel || !email || !password || !confirm) {
            alert("");
            return;
        }

        //  ( Gmail)
        const emailRegex = /^[a-zA-Z0-9._%+-]+@gmail\.com$/;
        if (!emailRegex.test(email)) {
            alert(" ( Gmail )");
            return;
        }

        //  ( 10   0)
        const phoneRegex = /^0[0-9]{9}$/;
        if (!phoneRegex.test(tel)) {
            alert(" (10  0)");
            return;
        }

        // 
        if (password.length < 6) {
            alert(" 6 ");
            return;
        }

        if (password !== confirm) {
            alert(" ");
            return;
        }

        // ,  
        const nameRegex = /^[A-Za-z----]+$/;  // Only letters and Thai characters
        if (!nameRegex.test(firstname) || !nameRegex.test(lastname)) {
            alert("");
            return;
        }

        //  ()
        const licenseRegex = /^[0-9-]+$/;  // Only numbers
        if (!licenseRegex.test(license)) {
            alert("");
            return;
        }

        try {
            let imageUrl = defaultImage;

            if (image) {
                const storageRef = firebase.storage().ref();
                const imageRef = storageRef.child(`images/${image.name}`);
                await imageRef.put(image);
                imageUrl = await imageRef.getDownloadURL();
                console.log("Image uploaded successfully. URL:", imageUrl);
            }

            const nutrData = {
                firstname,
                lastname,
                password,
                license_number: license,
                tel,
                email,
                image_profile: imageUrl,
                image_background: defaultBackground,
                isDeleted: false
            };

            console.log("User Data:", nutrData);

            const response = await axios.post(`${apiAddress}/register`, nutrData);
            console.log("Response from server:", response);

            if (response.status === 201) {
                login(response.data.token, response.data.user);
                alert(" ");
                navigate('/signin');
            }
        } catch (error) {
            alert("");
            console.log("error sign up", error);
            if (error.response) {
                console.log("Error response data:", error.response.data);
            }
        }
    };

    const handleImageChange = (e) => {
        const selectedFile = e.target.files[0];
        if (selectedFile) {
            setImage(selectedFile);
        } else {
            console.log("No file selected!");
        }
    };

    const triggerFileInputClick = () => {
        document.getElementById('imageUpload').click();
    };

    return (
        <div className='sign-up--container'>
            <div className='signup-card'>
                <h1></h1>
                <div className='signup-form'>
                    <div className='signup-form--left'>
                        {image ? (
                            <img
                            loading="lazy"
                                className='signup-form--image'
                                alt="" 
                                onClick={triggerFileInputClick}
                                src={URL.createObjectURL(image)}
                            />
                         ) : (
                            <img
                            loading="lazy"
                                className='signup-form--image'
                                alt=""
                                onClick={triggerFileInputClick}
                                src={defaultImage}
                            />
                         )}
                        <input 
                            type="file"
                            id="imageUpload"
                            onChange={handleImageChange} />
                    </div>
                    <div className='signup-form--right'>
                        <label htmlFor='signup-firstname'></label>
                        <Input className='form--inputbox' value={firstname} onChange={(e) => setFirstname(e.target.value)} />

                        <label htmlFor='signup-lastname'></label>
                        <Input className='form--inputbox' value={lastname} onChange={(e) => setLastname(e.target.value)} />

                        <label htmlFor='signup-license'></label>
                        <Input className='form--inputbox' value={license} onChange={(e) => setLicense(e.target.value)} />

                        <label htmlFor='signup-tel'></label>
                        <Input className='form--inputbox' value={tel} onChange={(e) => setTel(e.target.value)} />

                        <label htmlFor='signup-email'></label>
                        <Input className='form--inputbox' value={email} onChange={(e) => setEmail(e.target.value)} />

                        <label htmlFor='signup-pass'></label>
                        <Input className='form--inputbox' type='password' value={password} onChange={(e) => setPassword(e.target.value)} />

                        <label htmlFor='signup-pass--confirm'></label>
                        <Input className='form--inputbox' type='password' value={confirm} onChange={(e) => setConfirm(e.target.value)} />

                        <button className='signup-btn' onClick={handleSignup}></button>
                        <b>? <p onClick={() => navigate('/signin')}></p></b>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default RegisterScreen;
