import React, { useEffect, useState} from 'react'
import Navbar from '../../components/Navbar/Navbar'
import SideBar from '../../components/SideBar/SideBar'
import { Select } from "antd";
import '../../App.css'
import { useLocation, useNavigate } from 'react-router-dom';
import { useAuth } from '../../middleware/Auth';
import axios from 'axios';
import '../../components/trivia.css'
const optionsDMY = {
     timeZone: "Asia/Bangkok",
     year: 'numeric',
     month: 'long',
     day: 'numeric',
};

function TriviaScreen() {
     const navigate = useNavigate();
     const { nutrData } = useAuth();

     const location = useLocation();
const { triviaData } = location.state || {};

     const [trivs, setTrivia] = useState([]);
     const [trivsUser, setTriviaUser] = useState([]);
   
     const [searchTriv, setSearchTriv] = useState('');
     const [selectedType, setSelectedType] = useState("");
     const [selectedDisplay, setSelectedDisplay] = useState("");

       const [currentPage, setCurrentPage] = useState(1);
          const itemsPerPage = 4;

     const [activeButton, setActiveButton] = useState('');

     useEffect(() => {
          const fetchTriviaData = async () => {
               try {
                    const response = await axios.get("https://gouthiw-health.onrender.com/trivias", { timeout: 1000 });
                    setTrivia(response.data);
               } catch (error) {
                    console.log("Error fetching trivias data", error.message)
               }
          }
          const fetchTriviaDataUser = async () => {
               try {
                    const response = await axios.get(`https://gouthiw-health.onrender.com/trivias/auth/${nutrData._id}`, { timeout: 1000 });
                    setTriviaUser(response.data);
               } catch (error) {
                    console.log("Error fetching trivias data", error.message)
               }
          }
          fetchTriviaData();
          if (nutrData) {
               fetchTriviaDataUser();
          }
     }, [nutrData])

     const filteredTrivs = trivs.filter(triv => 
          (selectedType === "" || triv.trivia_type === selectedType) &&
          triv.head.includes(searchTriv)
     );

     // 
     const filterDisplay = selectedDisplay === ""
          ? filteredTrivs.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt)) // 
          : selectedDisplay === "last_update"
          ? filteredTrivs.sort((a, b) => new Date(b.updatedAt) - new Date(a.updatedAt)) // 
          : filteredTrivs;

     const stripHTML = (html) => {
          const div = document.createElement('div');
          div.innerHTML = html;
          return div.textContent || div.innerText || '';
     };

     const paginate = (pageNumber) => setCurrentPage(pageNumber);

     const renderItem = (item) => (
          <div className='trivia-card' onClick={() => handleItemPress(item._id)} key={item._id}>
               <img className='trivia-pic' alt={` ${item.head}`} src={item.image} loading="lazy"/>
               <div className='trivia-info'>
                    <h1>{item.head}</h1>
                    <p className='trivia-date'> {calculateTimeAgo(item.updatedAt)}</p>
                    <div className='trivia-des'>
                         <p>{stripHTML(item.content)}</p>
                    </div>
               </div>
          </div>
     );
     
     const handleItemPress = async (itemId) => {
          try {
               const response = await axios.get(`https://gouthiw-health.onrender.com/trivia/${itemId}`);
               const triviaData = response.data;

               console.log("triviaData: ", triviaData)
               navigate('/trivia-detail', { state: { triviaData } });
          } catch (error) {
               console.log('Error fetching trivia data', error.message);
          }
     };

     const calculateTimeAgo = (createdAt) => {
          const currentTime = new Date();
          const postTime = new Date(createdAt);
          const timeDiff = Math.abs(currentTime - postTime) / 36e5;  // 
          
          if (timeDiff < 1) {
               return `${Math.floor(timeDiff * 60)} `;
          } else if (timeDiff < 24) {
               return `${Math.floor(timeDiff)} `;
          } else {
               return postTime.toLocaleString("th-TH", optionsDMY);
          }
     };

     const indexOfLastItem = currentPage * itemsPerPage;
     const indexOfFirstItem = indexOfLastItem - itemsPerPage;
     const currentItems = filterDisplay.slice(indexOfFirstItem, indexOfLastItem);

     const totalPages = Math.ceil(filterDisplay.length / itemsPerPage);

     return (
          <>
               <div className='container'>
                    <Navbar />
                    <div className='sidebar-content-wrapper'>
                         <SideBar/>
                         <div className='content'>
                              <div className='main-content'>
                                   <div className='trivia-content'>
                                        <div className='display-flex'>
                                             <p className='breadcumb'>
                                                  <span className='press-to-back'></span>
                                                  <span className='gray-color'> &#62;</span> 
                                             </p>
                                             <div className='divider' />
                                             <button className='add-trivia-btn' onClick={() => navigate('/trivia')}>
                                                  <i className="fa-solid fa-plus"> </i>
                                             </button> 
                                        </div>

                                        <h1 className='head-content'></h1>
                                        <div className='trivia-manage'>
                                             <div className='trivia-search'>
                                                  <div className='trivia-search-wrapper'>
                                                       <i className="fa-solid fa-magnifying-glass trivia-search-icon"></i>
                                                       <input 
                                                            type='text'
                                                            placeholder='' 
                                                            onChange={(e) => setSearchTriv(e.target.value)} 
                                                            
                                                            className='trivia-search-input' />
                                                  </div>

                                                  <div className='trivia-select-wrapper'>
                                                       <i className="fa-solid fa-filter trivia-search-icon"></i>
                                                       <Select 
                                                            className='trivia-search-select'
                                                            value={selectedType} 
                                                            onChange={(value) => setSelectedType(value)} //  selectedFilterType 
                                                            options={[
                                                                 { value: "", label: "" },
                                                                 { value: "", label: "" },
                                                                 { value: "", label: "" },
                                                                 { value: "", label: "" },
                                                                 { value: " ", label: " " },
                                                            ]}
                                                       />
                                                  </div>

                                                  <div className='trivia-select-wrapper'>
                                                       <i className="fa-solid fa-sort trivia-search-icon"></i>
                                                       <Select 
                                                            className='trivia-search-select'
                                                            value={selectedDisplay} 
                                                            onChange={(value) => setSelectedDisplay(value)} //  selectedFilterType 
                                                            options={[
                                                                 { value: "last_add", label: "" },
                                                                 { value: "last_update", label: "" }
                                                            ]}
                                                       />
                                                  </div>
                                             </div>
                                        </div>
                                   </div>

                                   <div className='above-table'>
                                        <p> {trivs.length} </p>
                                        <div className='switch-btn'>
                                             <button 
                                                  onClick={() => setActiveButton('')}
                                                  style={{
                                                       backgroundColor: activeButton === '' ? '#FFA13F' : 'white',
                                                       color: activeButton === '' ? 'white' : 'black'
                                                  }}></button>
                                             <button 
                                                  onClick={() => setActiveButton('')}
                                                  style={{
                                                       backgroundColor: activeButton === '' ? '#FFA13F' : 'white',
                                                       color: activeButton === '' ? 'white' : 'black'
                                                  }}></button>
                                        </div>
                                   </div>
                                   
                                   <div className='trivia-render'>
                                        {activeButton === '' ? (
                                             currentItems.length > 0 ? (
                                                  currentItems.map(item => renderItem(item))
                                             ) : (
                                                  <h2></h2>
                                             )
                                        ) : (
                                             trivsUser.length > 0 ? (
                                                  trivsUser.map(item => renderItem(item))
                                             ) : (
                                                  <h2></h2>
                                             )
                                        )}
                                   </div>


                                   {/* Pagination controls */}
                                   <div className="pagination">
                                        <button
                                             onClick={() => paginate(currentPage - 1)}
                                             disabled={currentPage === 1}
                                             className="pagination-button"
                                        >
                                             
                                        </button>
                                        {[...Array(totalPages).keys()].map(number => (
                                             <button
                                                  key={number}
                                                  onClick={() => paginate(number + 1)}
                                                  className={`pagination-button ${currentPage === number + 1 ? 'active' : ''}`}
                                             >
                                                  {number + 1}
                                             </button>
                                        ))}
                                        <button
                                             onClick={() => paginate(currentPage + 1)}
                                             disabled={currentPage === totalPages}
                                             className="pagination-button"
                                        >
                                             
                                        </button>
                                   </div>
                              </div>
                         </div>     
                    </div>
                    
               </div>
          </>
     )
}

export default TriviaScreen
